html, body {
    height: 100%;
    width: 100%;
    padding: 0;
    margin: 0;
}

body {
    background: url('https://htmlcolorcodes.com/assets/images/html-color-codes-color-tutorials-903ea3cb.jpg') no-repeat fixed center;
    background-size: cover;
    font-family: 'Roboto', sans-serif;
    color: white;
    display: flex;
    flex-direction: column;
}

header {
	background: black;
    border-bottom: 4px solid #4C9CF1;
}

header .logo {
    height: 50px;
    margin-top: 15px;
}

main {
    flex: auto;
}

footer {
    background: #070617;
    border-top: 4px solid #4C9CF1;
}

footer img {
    width: 120px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    margin-top: 15px;
    position: relative;
    animation: move 5s alternate infinite linear;
}

.clip-svg {
    clip-path: url(#clip);
}

@keyframes move {
    0%   {left: 0px;}
    25%  {left: 100px;}
    50%  {left: 0px;}
    75%  {left: -100px;}
    100% {left: 0px;}
}

h1, h2 {
    text-align: center;
}

header h1 {
    animation: title-header 3s; 
}

@keyframes title-header {
    0%{
      transform: rotateY(90deg);
      opacity: 0.0;
    }
    100%{
      transform: rotateY(0deg);
      opacity: 1.0;
    }
}

h2 {
    animation: second-title 6s; 
}

@keyframes second-title {
    0%{
        opacity: 0;
        transform: translateY(-1rem) scale(.8);
    }
    100% {
        opacity: 1;
        transform: none;
    }
}

.button {
    background-color: #008CBA;;
    border: none;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
    color: white;
}

a {
    color: blue;
}

main span:hover {
    animation: scale-link 2s infinite;
}

@keyframes scale-link {
    50% {
        transform: scale(1.5);
    }
    100% {
        transform: scale(1);
    }
}

.keypad {
    margin-top: 15px;
    text-align: right;
    padding: 30px;
}

img {
    display: block;
    margin: 0 auto;
    max-width: 100%;
}

main img {
    transition:all .9s ease;
    cursor: pointer;
}

ul {
    list-style: none;
    margin: 0 auto;
    padding: 0;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    flex-direction: column;
}

li {
    display: inline;
    margin: 0 10px;
}

li img {
    height: 150px;
}

.container-header, .container {
    display: flex;
    flex-flow: row wrap;
}

.container {
    justify-content: space-around;
}

main .container {
    padding: 30px;
}

.category, .article-list, article, .other-articles {
    width: 100%;
    background-color: white;
    color: black;
    line-height: 75px;
    border-radius: 10px;
    text-align: center;
    box-shadow: 11px 13px 18px 0px rgba(0,0,0,0.75);
    margin-top: 10px;
}

.element-footer, .element-header {
    width: 100%;
}

article {
    padding: 5px;
}

article p {
    text-align: left;
    line-height: 1.4;
    padding: 5px;
    animation: 5s paragraph-move;
}

@keyframes paragraph-move {
    from {
      margin-left: 50%;
    }
  
    to {
      margin-left: 0%;
    }
}

video {
    max-width: 100%;
    height: auto;
}

@media (min-width: 600px) {
    .category {
      width: 45%;
    }

    .article-list {
        width: 100%;
    }

    article {
        width: 60%;
    }

    .other-articles {
        width: 35%;
    }

    .element-footer {
        width: 25%;
    }

    .element-header {
        width: 33.3%;
    }

    ul {
        flex-direction: row;
    }
}